body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.app-version {
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.graph_container{
  width: 100%;
  height: 100%;
}
.graph {
  background-color: steelblue;
  z-index: 1;
  cursor: default;
}

.focused {
	stroke: black !important;
}

.btnn {
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 10rem;
  position: relative;
  font-size: 12px;
  height: 30px;
  align-self: center;
  background-color: cadetblue;
  border: none;
  color: aliceblue;
  -webkit-transition: all .2s;
  transition: all .2s;
}

.btnn:hover{
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 1rem 2rem rgba(0,0,0,.2)
}

.btnn:hover::after{
  -webkit-transform: scaleX(1.4) scaleY(1.6);
          transform: scaleX(1.4) scaleY(1.6);
  opacity:0;
}

.btnn:active,
.btnn:focus{
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
  outline:0;
}

.graphEditPanel {
  position: absolute;
  max-width: 50%;
  background-color: lightgray;
  bottom: 0;
  z-index: 2;
  margin: 5px;
  padding: 5px;
  text-align: center;
}

.CloseEditPanelButton {
  position: absolute;
  top: 1px;
  right: 1px;
}

.CloseEditPanelButton svg {
  width: 18px;
  height: 18px;
}

.cursor-pointer {
  cursor: pointer;
}

.links-list {
  max-height: 400px;
  overflow: auto;
}

.SavePanel {
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
  max-width: 50%;
  background-color: lightgray;
  z-index: 2;
  margin: 5px;
  padding: 5px;
}

.GraphNameInput {
  margin-top: 0px !important;
  margin-bottom: 5px !important;
}

.ProjectPublicity {
  font-size: 12px;
}

.ProjectPublicity svg {
  font-size: 12px;
}

.greenPoint {
  background-color: green;
  padding: 3px;
}

.redPoint {
  background-color: red;
  padding: 3px;
}

.gistSelection {
  text-align: center;
}

.gistDescription {
  border: 3px outset black;
  padding: 5px;
  margin: 3px;
}

.yellow {
  background: #fc3;
}

.blue {
  background: skyblue;
}

.settingsPanel {
  position: absolute;
  top: 0;
  right: 0;
  max-width: 50%;
  background-color: lightgray;
  z-index: 2;
  margin: 5px;
  padding: 5px;
  text-align: center;
}

