.graphEditPanel {
  position: absolute;
  max-width: 50%;
  background-color: lightgray;
  bottom: 0;
  z-index: 2;
  margin: 5px;
  padding: 5px;
  text-align: center;
}

.CloseEditPanelButton {
  position: absolute;
  top: 1px;
  right: 1px;
}

.CloseEditPanelButton svg {
  width: 18px;
  height: 18px;
}

.cursor-pointer {
  cursor: pointer;
}

.links-list {
  max-height: 400px;
  overflow: auto;
}
